.background-first-screen {
  background: linear-gradient(228.57deg, #323264 11.93%, #323264 57.17%, #643E72 87.46%);
  border-radius: 0px 0px 12px 12px;
  position: relative;
}

.background-first-screen::after, .background-first-screen::before {
  content: "";
  border-radius: 0px 0px 12px 12px;
  height: 10px;
  position: absolute;
}

.background-first-screen::before {
  background: #CACADB;
  bottom: -10px;
  left: 20px;
  right: 20px;
}

.background-first-screen::after {
  background: #E7E7F2;
  bottom: -20px;
  left: 40px;
  right: 40px;
}

