    margin: 0;
  }
}

@media screen and (min-width: 768px) and (max-width: 991px) {
  .footer__body_about {
    justify-content: flex-start;
    max-width: 400px;
    margin: 0 auto;
  }
}

.footer__border {
  border-top: 2px solid #E6E6F0;
  margin: 0 15px;
}

@media screen and (min-width: 768px) {
  .footer__border {
    margin: 0;
  }
}

.footer__top {
  padding-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (min-width: 768px) {
  .footer__top {
    flex-direction: row;
    order: 0;
    width: 50%;
  }
}

@media screen and (min-width: 768px) and (max-width: 991px) {
  .footer__top_about {
    order: 0;
    width: 100%;
  }
}

.footer__top-text {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #323264;
}

.footer__top-link {
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #24A3FF;
}

@media screen and (min-width: 768px) {
  .footer__top-link {
    display: flex;
    align-items: center;
  }
}

.footer__bottom {
  padding: 18px 0 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (min-width: 768px) {
  .footer__bottom {
    order: 2;
    flex-direction: row;
    padding-bottom: 0;
  }
}

@media screen and (min-width: 768px) and (max-width: 991px) {
  .footer__bottom_about {
    order: 0;
  }
}

.footer__bottom-tel {
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  text-align: right;
  color: #46466E;
  padding: 10px 0;
}

@media screen and (min-width: 768px) {
  .footer__bottom-tel {
    display: flex;
    align-items: center;
  }
}

.footer__bottom-mail {
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #24A3FF;
}

@media screen and (min-width: 768px) {
  .footer__bottom-mail {
    display: flex;
    align-items: center;
  }
}

.footer__right {
  display: flex;
  justify-content: center;
}

@media screen and (min-width: 768px) {
  .footer__right {
    order: 1;
    padding-top: 30px;
  }
}

@media screen and (min-width: 768px) and (max-width: 991px) {
  .footer__right_about {
    order: 0;
  }
}

.footer__right-link {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #B3B3BA;
}

@media screen and (min-width: 768px) {
  .footer__top-link::before, .footer__bottom-tel::before, .footer__bottom-mail::before {
    content: "";
    width: 6px;
    height: 6px;
    background: #B3B3BA;
    border-radius: 50%;
    margin: 0 10px;
    display: block;
  }
}

.top-block {
  padding: 20px 0 30px;
}

@media screen and (min-width: 768px) {
  .top-block {
    padding: 60px 0;
  }
}

@media screen and (min-width: 1200px) {
  .top-block {
    max-width: 1110px;
    margin: 0 auto;
  }
}

.top-block__wrapper {
  padding: 0 15px;
}

@media screen and (min-width: 768px) {
  .top-block__wrapper {
    padding: 0;
  }
}

@media screen and (min-width: 992px) {
  .top-block__wrapper {
    display: flex;
    align-items: flex-start;
  }
}

@media screen and (min-width: 992px) {
  .top-block__text-left {
    flex-basis: 56%;
  }
}

.top-block__text-right {
  margin: 15px 0;
  border-left: 4px solid #E6E6F0;
}

@media screen and (min-width: 992px) {
  .top-block__text-right {
    flex-basis: 40%;
    margin: 0;
    margin-left: 30px;
  }
}

.top-block__item {
  padding-left: 30px;
}

.top-block__item:not(:last-child) {
  padding-bottom: 40px;
}

.top-block__item-title {
  color: transparent;
  background-clip: text;
  background-image: linear-gradient(90.13deg, #F0145A 0.84%, #EF8080 99.96%);
  font-weight: 700;
  font-size: 36px;
  line-height: 44px;
  padding-left: 8px;
}

.top-block__item-subtitle {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #323264;
  padding-top: 10px;
}

.top-block__item-top {
  display: flex;
  align-items: center;
}

.text {
  font-weight: 400;
  font-size: 19px;
  line-height: 30px;
  color: #000000;
  padding: 15px 0;
}

.center-about {
  background-image: url(../img/about-backgroung.png);
  background-color: #B3B3BA;
  background-repeat: no-repeat;
  background-size: cover;
  height: 114px;
  width: 100%;
  font-weight: 700;
  font-size: 36px;
  line-height: 44px;
  letter-spacing: 13px;
  text-transform: uppercase;
  color: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 768px) {
  .center-about {
    font-weight: 700;
    font-size: 46px;
    line-height: 56px;
    letter-spacing: 63px;
    height: 224px;
  }
}

@media screen and (min-width: 992px) {
  .center-about {
    font-size: 86px;
    line-height: 105px;
    height: 312px;
  }
}

@media screen and (min-width: 1200px) {
  .center-about {
    font-size: 96px;
    line-height: 117px;
    height: 438px;
  }
}

.bottom-block {
  padding: 30px 0 60px;
}

@media screen and (min-width: 768px) {
  .bottom-block {
    padding: 60px 0;
  }
}

@media screen and (min-width: 1200px) {
  .bottom-block {
    max-width: 1110px;
    margin: 0 auto;
  }
}

.bottom-block__wrapper {
  padding: 0 15px;
}

@media screen and (min-width: 992px) {
  .bottom-block__wrapper {
    display: flex;
    justify-content: space-between;
  }
}

@media screen and (min-width: 992px) {
  .bottom-block__text-block {
    width: 49%;
  }
}

@media screen and (min-width: 992px) {
  .bottom-block__title {
    width: 42%;
  }
}

.bottom-block__text {
  padding-bottom: 30px;
  font-weight: 400;
  font-size: 19px;
  line-height: 30px;
  color: #000000;
}

.achievements {
  background: #F5F5FA;
  padding: 30px 0;
}

.achievements__wrapper {
  padding: 0;
}

.achievements__title {
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #323264;
}

.achievements__list {
  padding: 30px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 768px) {
  .achievements__list {
    justify-content: space-between;
  }
}

.achievements__card {
  width: 255px;
  height: 90px;
  background: #FFFFFF;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 768px) {
  .achievements__card {
    width: 22%;
  }
}

@media screen and (min-width: 992px) {
  .achievements__card {
    height: 110px;
  }
}

.achievements__card_none {
  display: none;
}

@media screen and (min-width: 768px) {
  .achievements__card_none {
    display: flex;
  }
}

.achievements__image {
  width: 21%;
  height: 40px;
}

@media screen and (min-width: 768px) {
  .achievements__image {
    width: auto;
  }
}

@media screen and (min-width: 992px) {
  .achievements__image {
    height: 60px;
  }
}
